#root, .chapters, .content, .subchapters, .dashboard{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.chapters{
    position: fixed;
    z-index: 101;
    top: 0;
}

.subchapters{
    position: fixed;
    z-index: 101;
    top: 0vh;
    left: 0;
    flex-direction: row;
}

.dashboard{
    position: fixed;
    z-index: 101;
    top: 0vh;
    right: 0;
    flex-direction: row;
}

.content{
    width: 70vw;
    margin-top: 20vh;
}

.subchapter-section{
    overflow: auto;
    max-height: 30vh;
    flex-wrap: nowrap;
}

.exercise, .nav-buttons{
    z-index: 100;
    position: relative;
}

.nav-link.active.section{
    background-color: #0dc1fd;
}

.modal fade {
    z-index: 1040 !important;
}
.modal-content {
    margin: 2px auto;
    z-index: 1100 !important;
}

.login > *, .register > * {
    margin-right: 0.5em;
}

.dashboard-logged-in > div {
    margin-bottom: 1.2em;
}